.container {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 99.9%;
    min-width: 480px;
}

.breadcrumb {
    display: flex;
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    overflow: hidden;
    margin: auto;
    text-align: center;
    top: 50%;
    width: 98%;
    height: 45px;
    z-index: 1;
    font-size: 13px;
}

.breadcrumb a {
    position: relative;
    display: flex;
    flex-grow: 1;
    text-decoration: none;
    margin: auto;
    height: 100%;
    padding-left: 50px;
    padding-right: -10px;
    color: #000;
    background-color: #ffff00;
}

.breadcrumb a:first-child {
    padding-left: 12px;
}

.breadcrumb a:last-child {
    padding-right: 12px;
}

.breadcrumb a:after {
    content: "";
    position: absolute;
    display: inline-block;
    width: 45px;
    height: 45px;
    top: 0;
    right: -23px;
    background-color: #ffff00;
    transform: scale(1) rotate(45deg);
    box-shadow: 5px -5px rgba(0, 0, 0, 1);
    z-index: 1;
}

.breadcrumb a:last-child:after {
    content: none;
}

.breadcrumb__inner {
    display: flex;
    flex-direction: column;
    margin: auto;
    background-color: #ffff00;
    color: #000;
    border-radius: 2em;
    z-index: 2;
    box-shadow: 0 0 10px 10px rgba(255, 255, 0, 1);
}

.breadcrumb__title {
    font-weight: bold;
}

.breadcrumb a.chapter, .breadcrumb a.chapter:after {
    background: red;
    color: white;
}

.breadcrumb a.active, .breadcrumb a:hover {
    background: #ff0000;
    color: white;
}

.breadcrumb a.active:after, .breadcrumb a:hover:after {
    background: #ff0000;
    color: white;
}

.breadcrumb a.active{
    background: #000;
    color: white;
}

.breadcrumb a.active:after{
    background: #000;
    color: white;
}

@media all and (max-width: 1000px) {
    .breadcrumb {
        font-size: 11px;
    }
}
@media all and (max-width: 710px) {
    .breadcrumb__desc {
        display: none;
    }

    .breadcrumb {
        height: 30px;
    }

    .breadcrumb a {
        padding-left: 20px;
    }

    .breadcrumb a:after {
        content: "";
        width: 30px;
        height: 30px;
        right: -15px;
        transform: scale(0.707) rotate(45deg);
    }
}