.mainBox {
    display: grid;
    grid-template-rows: 1fr 40px;
    grid-template-areas:
        'l'
        'p';
    margin: 10px auto;
    padding: 1px;
    max-width: 400px;
    height: 315px;
    background-color: darkslategray;
    border-radius: 10px;
}

.mainBox button {
    cursor: pointer;
    position: relative;
    margin: 0 auto 0 0;
    width: 25%;
    font-size: 14pt;
    border-radius: 5px;
}

.buttonAll {
    background-color: aqua;
}

.buttonAll:hover {
    background-color: cornflowerblue;
}

.buttonHigh {
    background-color: red;
}

.buttonHigh:hover {
    background-color: orangered;
}

.buttonMid {
    background-color: yellow;
}

.buttonMid:hover {
    background-color: #ffff55;
}

.buttonLow {
    background-color: green;
}

.buttonLow:hover {
    background-color: limegreen;
}

.buttonsBlock {
    grid-area: p;
    position: relative;
    margin: auto 2%;
}

.affairsBlock {
    grid-area: l;
}

.affair {
    display: grid;
    grid-template-columns: 1fr 30px;
    grid-template-areas: 'a b';
    grid-area: a;
    margin: 2%;
    padding: 5px;
    border: 2px groove lightblue;
    border-radius: 5px;
    font-size: 14pt;
    font-weight: bold;
}

.affair button {
    cursor: pointer;
    grid-area: b;
    width: 30px;
    height: 30px;
    padding: -1px 0 5px 0;
    font-weight: bold;
    font-size: 20pt;
    text-indent: -0.5pt;
    background-color: red;
    border-radius: 20px;
    border-width: 1px;
}

.affair button:focus {
    outline: none;
}

.affair button:hover {

    border: 1px solid darkred;
    background-color: orangered;
    color: #0f0f0f;
}
.affair button:active {
    border: 1px ridge red;
    background-color: red;
    color: white;
}
